import React from 'react';

// Material UI Components
import { Typography } from '@material-ui/core';

// Style sheet
import Style from './Content.module.scss';

export function Content({ locationPicked, local}){
    const location = (locationPicked === undefined || locationPicked === null) ? local : locationPicked;

    // Function to calculate the local time of the location
    function calcTime(timezone, type){
        const date = new Date();
        const time = date.setSeconds(timezone)
        const locationDate = new Date(time);

        const dateObject = {
            hours: locationDate.getUTCHours(),
            minutes: locationDate.getUTCMinutes(),
            day: locationDate.getDay(),
            dayText: "",
            date: locationDate.getDate(),
            month: locationDate.getMonth(),
            monthText: "",
            year: locationDate.getFullYear(),
            timeOfDay: "",
        }

        switch(dateObject.day){
            case 0:
                dateObject.dayText = "Sunday";
                break;
            case 1:
                dateObject.dayText = "Monday";
                break;
            case 2:
                dateObject.dayText = "Tuesday";
                break;
            case 3:
                dateObject.dayText = "Wednesday";
                break;
            case 4:
                dateObject.dayText = "Thursday";
                break;
            case 5:
                dateObject.dayText = "Friday";
                break;
            case 6:
                dateObject.dayText = "Saturday";
                break;
            default:
                break;
        }
        
        switch(dateObject.month){
            case 0:
                dateObject.monthText = "January";
                break;
            case 1:
                dateObject.monthText = "February";
                break;
            case 2:
                dateObject.monthText = "March";
                break;
            case 3:
                dateObject.monthText = "April";
                break;
            case 4:
                dateObject.monthText = "May";
                break;
            case 5:
                dateObject.monthText = "June";
                break;
            case 6:
                dateObject.monthText = "July";
                break;
            case 7:
                dateObject.monthText = "August";
                break;
            case 8:
                dateObject.monthText = "September";
                break;
            case 9:
                dateObject.monthText = "October";
                break;
            case 10:
                dateObject.monthText = "November";
                break;
            case 11:
                dateObject.monthText = "December";
                break;
            default:
                break;
        } 

        
        if(dateObject.minutes >= 0 && dateObject.minutes < 10){
            dateObject.minutes = `0${dateObject.minutes}`
        }

        if(dateObject.hours >= 0 && dateObject.hours < 12){
            dateObject.timeOfDay = "AM";
        } else{

            switch(dateObject.hours){
                case 13:
                    dateObject.hours = 1;
                    break;
                case 14:
                    dateObject.hours= 2;
                    break;
                case 15:
                    dateObject.hours= 3;
                    break;
                case 16:
                    dateObject.hours= 4;
                    break;
                case 17:
                    dateObject.hours= 5;
                    break;
                case 18:
                    dateObject.hours= 6;
                    break;
                case 19:
                    dateObject.hours= 7;
                    break;
                case 20:
                    dateObject.hours= 8;
                    break;
                case 21:
                    dateObject.hours= 9;
                    break;
                case 22:
                    dateObject.hours= 10;
                    break;
                case 23:
                    dateObject.hours= 11;
                    break;
                default:
                    break;
            } 

            dateObject.timeOfDay = "PM";
        }

        if(dateObject.hours >= 0 && dateObject.hours < 10){
            dateObject.hours = `0${dateObject.hours}`
        }
        
        if(type === "time"){
            return `${dateObject.hours}:${dateObject.minutes}`
        }else if(type === "timeOfDay"){
            return dateObject.timeOfDay
        } else if(type === "date"){
            return `${dateObject.dayText}, ${dateObject.monthText} ${dateObject.date}, ${dateObject.year}`
        }
    }

    return(
        <div className={Style.Content}>
            <Typography className={Style.Subtitle} variant="button" align="center">{location.name === "Local" ? location.name : `${location.name} (${location.sys.country})`}</Typography>
            <Typography className={Style.Title} variant="h1" color="primary" align="center">
                {calcTime(location.timezone, "time")}
                <span><Typography variant="button" color="primary" align="center">{calcTime(location.timezone, "timeOfDay")}</Typography></span>
            </Typography>
            <Typography className={Style.Subtitle} variant="button" align="center">{calcTime(location.timezone, "date")}</Typography>
        </div>
    );
}