import React from 'react';

// Material UI Components
import { Typography, Tooltip, Button, Box,} from '@material-ui/core';

// Style sheet
import Style from './Time.module.scss';

// Icons
import KeyboardArrowDownIcon from '@material-ui/icons/KeyboardArrowDown';
import KeyboardArrowUpIcon from '@material-ui/icons/KeyboardArrowUp';

export function Time({timerOn, seconds, minutes, hours, handleTime}){
    return(
        <div className={Style.Time}>
            <div className={Style.TimeGroup}>
                <Typography variant="overline" align="center">Hours</Typography>
                <Tooltip className={timerOn === true ? Style.Hidden :Style.Tooltip} title={hours === 23 ? "" : "Add 1 hour"} aria-label="Add 1 hour" placement="top">
                    <span>
                        <Button 
                            onClick={hours >= 0 && hours < 23 ? () => handleTime("Add", "Hour") : handleTime()} 
                            className={timerOn === true ? Style.Hidden : hours >= 0 && hours < 23 ? Style.GreyBtn : ""} 
                            variant="outlined" 
                            size="large" 
                            fullWidth 
                            disabled={hours >= 0 && hours < 23 ? false : true} >
                                <KeyboardArrowUpIcon/>
                        </Button>
                    </span>
                </Tooltip>
                <Typography variant="h2" color="primary" align="center">
                    {hours >= 0 && hours < 10 ? String(`0${hours}`) : hours}
                </Typography>
                <Tooltip className={timerOn === true ? Style.Hidden :Style.Tooltip} title={hours === 0 ? "" : "Subtract 1 hour"} aria-label="Subtract 1 hour" placement="bottom">
                    <span>
                        <Button 
                            onClick={hours === 0 ? handleTime() : () => handleTime("Subtract", "Hour")} 
                            className={timerOn === true ? Style.Hidden : hours === 0 ? "" : Style.GreyBtn} 
                            variant="outlined" 
                            size="large" 
                            fullWidth 
                            disabled={hours === 0 ? true : false} >
                                <KeyboardArrowDownIcon />
                        </Button>
                    </span>
                </Tooltip>
            </div>
            <Box mt={3}>
                <Typography variant="h2" color="primary" align="center">:</Typography>
            </Box>
            <div className={Style.TimeGroup}>
                <Typography variant="overline" align="center">Minutes</Typography>
                <Tooltip className={timerOn === true ? Style.Hidden :Style.Tooltip} title={minutes === 59 ? "" : "Add 1 minute"} aria-label="Add 1 minute" placement="top">
                    <span>
                        <Button 
                            onClick={minutes >= 0 && minutes < 59 ? () => handleTime("Add", "Minute") : handleTime()} 
                            className={timerOn === true ? Style.Hidden : minutes >= 0 && minutes < 59 ? Style.GreyBtn : ""} 
                            variant="outlined" 
                            size="large" 
                            fullWidth 
                            disabled={minutes >= 0 && minutes < 59 ? false : true} >
                                <KeyboardArrowUpIcon />
                        </Button>
                    </span>
                </Tooltip>
                <Typography variant="h2" color="primary" align="center">
                    {minutes >= 0 && minutes < 10 ? String(`0${minutes}`) : minutes}
                </Typography>
                <Tooltip className={timerOn === true ? Style.Hidden :Style.Tooltip} title={minutes === 0 ? "" : "Subtract 1 minute"} aria-label="Subtract 1 minute" placement="bottom">
                    <span>
                        <Button 
                            onClick={minutes === 0 ? handleTime() : () => handleTime("Subtract", "Minute")} 
                            className={timerOn === true ? Style.Hidden : minutes === 0 ? "" : Style.GreyBtn} 
                            variant="outlined" 
                            size="large" 
                            fullWidth 
                            disabled={minutes === 0 ? true : false} >
                                <KeyboardArrowDownIcon />
                        </Button>
                    </span>
                </Tooltip>
            </div>
            <Box mt={3}>
                <Typography variant="h2" color="primary" align="center">:</Typography>
            </Box>
            <div className={Style.TimeGroup}>
                <Typography variant="overline" align="center">Seconds</Typography>
                <Tooltip className={timerOn === true ? Style.Hidden :Style.Tooltip} title={seconds === 59 ? "" : "Add 1 second"} aria-label="Add 1 second" placement="top">
                    <span>
                        <Button 
                            onClick={seconds >= 0 && seconds < 59 ? () => handleTime("Add", "Second") : handleTime()} 
                            className={timerOn === true ? Style.Hidden : seconds >= 0 && seconds < 59 ? Style.GreyBtn : ""} 
                            variant="outlined" 
                            size="large" 
                            fullWidth 
                            disabled={seconds >= 0 && seconds < 59 ? false : true} >
                                <KeyboardArrowUpIcon />
                        </Button>
                    </span>
                </Tooltip>
                <Typography variant="h2" color="primary" align="center">
                    {seconds >= 0 && seconds < 10 ? String(`0${seconds}`) : seconds}
                </Typography>
                <Tooltip className={timerOn === true ? Style.Hidden :Style.Tooltip} title={seconds === 0 ? "" : "Subtract 1 second"} aria-label="Subtract 1 second" placement="bottom">
                    <span>
                        <Button 
                            onClick={seconds === 0 ? handleTime() : () => handleTime("Subtract", "Second")} 
                            className={timerOn === true ? Style.Hidden : seconds === 0 ? "" : Style.GreyBtn} 
                            variant="outlined" size="large" 
                            fullWidth 
                            disabled={seconds === 0 ? true : false} >
                                <KeyboardArrowDownIcon />
                        </Button>
                    </span>
                </Tooltip>
            </div>
        </div>
    );
}