import React from 'react';

// Material UI Components
import { List, ListItem, ListItemText } from '@material-ui/core';

// Style sheet
import Style from './Alarm.module.scss';

export function Alarm({alarmPicked, handleStop}){
    return(
        <List className={Style.List}>
            <ListItem button onClick={() => handleStop(alarmPicked)}>
                <ListItemText className={`${Style.ListBtn} ${Style.RedBtn}`} align="center" primary={"Stop"} />
            </ListItem>
        </List>
    );
}