import React from 'react';

// Material UI Components
import { Zoom, Paper, Tooltip, Button } from '@material-ui/core';

// Style sheet
import Style from './Paper.module.scss';

// Icons
import ViewListIcon from '@material-ui/icons/ViewList';
import DeleteForeverIcon from '@material-ui/icons/DeleteForever';
import AddIcon from '@material-ui/icons/Add';

export function PaperComponent({ locationPicked, handleDialogOpen}){
    const location = (locationPicked === null) ? {name: "Local"} : locationPicked;

    return(
        <Zoom timeout={500} in>
            <Paper className={Style.Paper} variant="outlined" >
                <Tooltip title="View Locations" aria-label="View Locations" placement="top">
                    <Button aria-label="View Locations" onClick={() => handleDialogOpen("Loc")} className={Style.BlackBtn} size="large"><ViewListIcon /></Button>
                </Tooltip> 
                <div>
                    <Tooltip title={location.name === "Local" ? "" : "Delete Location"} aria-label="Delete Locations" placement="top">
                        <span>
                            <Button 
                                aria-label="Delete Locations"
                                className={location.name === "Local" ? "" : Style.RedBtn}
                                onClick={() => handleDialogOpen("Del")} 
                                size="large" 
                                disabled={location.name === "Local" ? true : false}><DeleteForeverIcon /></Button>
                        </span>
                    </Tooltip>
                    <Tooltip title="Add Locations" aria-label="Add Locations" placement="top">
                        <Button aria-label="Add Locations" onClick={() => handleDialogOpen("Add")} className={Style.BlackBtn} size="large"><AddIcon /></Button>
                    </Tooltip>
                </div>
            </Paper>
        </Zoom>
    );
}