import React from 'react';
import PropTypes from 'prop-types';

// Material UI Components
import { Typography, Zoom, Button, List, ListItem, ListItemText, Fade } from '@material-ui/core';

// Components
import {TabsComponent} from '../Tabs/Tabs';

// Style sheet
import Style from './Stopwatch.module.scss';

export class Stopwatch extends React.Component{
    constructor(props){
        super(props);
        this.interval = null;
        this.state = {
            stopwatchOn: false,
            seconds: 0,
            minutes: 0,
            laps: [],
            quickest: null,
            quickestSeconds: null,
            slowest: null,
        }
    }

    static propTypes = {
        stopwatchOn: PropTypes.bool,
        seconds: PropTypes.number,
        minutes: PropTypes.number,
        laps: PropTypes.bool,
        quickest: PropTypes.object,
        quickestSeconds: PropTypes.number,
        slowest: PropTypes.object,
    }

    // This method will reset the state and will clear the interval once the user exits from the page
    componentWillUnmount(){
        this.handleReset();
        clearInterval(this.interval);
    }

    // This method will create a new lap and store it in the laps array
    handleLap = () => {
        const newLaps = [...this.state.laps];

        const newLap = {
            id: Math.random(),
            index: newLaps.length + 1,
            seconds: this.state.seconds,
            minutes: this.state.minutes,
        }

        newLaps.unshift(newLap);

        this.setState({
            laps: newLaps,
        })

        this.calculateQuickest(newLap, newLaps)
    }

    // This method will check if the new time that is lapped is the quickest or the slowest time
    calculateQuickest = (currentLap, laps) => {
        if(laps.length > 1){
            let lastLap = laps[1];

            const currentQuickestSeconds = (this.state.quickest.minutes > 0 ? this.state.quickest.minutes * 60 : 0) + this.state.quickest.seconds;
            const currentSlowestSeconds = (this.state.slowest.minutes > 0 ? this.state.slowest.minutes * 60 : 0) + this.state.slowest.seconds;
            const currentPreviousLapSeconds = (lastLap.minutes > 0 ? lastLap.minutes * 60 : 0) + lastLap.seconds;
            const currentLapSeconds = (currentLap.minutes > 0 ? currentLap.minutes * 60 : 0) + currentLap.seconds;

            const calculateCurrentLap = currentLapSeconds - currentPreviousLapSeconds;

            const quickestSeconds = (this.state.quickestSeconds === null) ? currentQuickestSeconds : this.state.quickestSeconds;

            if(calculateCurrentLap < quickestSeconds){
                this.setState({
                    quickest: currentLap,
                    quickestSeconds: calculateCurrentLap
                })
                return
            } else if(calculateCurrentLap > currentSlowestSeconds){
                this.setState({
                    slowest: currentLap
                })
                return
            }else{
                return
            }
        } else{
            this.setState({
                quickest: currentLap,
                slowest: currentLap
            })
            return
        }
    }

    // This method will update the Laps Array and the stopwatch to the data from the lap that the user chooses. Then it will call the handleStop method. 
    handleUpdateStopwatch = (index) => {
        const currentLaps = [...this.state.laps];

        const newLaps = currentLaps.filter(lap => lap.index <= index);

        const lap = newLaps[0];

        const quickest = (newLaps.find(lap => lap.id === this.state.quickest.id) === true) ? this.state.quickest : newLaps[newLaps.length - 1];
        const slowest = (newLaps.find(lap => lap.id === this.state.slowest.id) === true) ? this.state.slowest : newLaps[newLaps.length - 1];

        this.setState(state => ({
            stopwatchOn: (state.stopwatchOn === false) ? false : !state.stopwatchOn,
            seconds: lap.seconds,
            minutes: lap.minutes,
            laps: newLaps,
            quickest: quickest,
            quickestSeconds: null,
            slowest: slowest,
        }))
        
        clearInterval(this.interval);
    }

    // This method will reset the stopwatch
    handleReset = () => {
        this.setState({
            stopwatchOn: false,
            seconds: 0,
            minutes: 0,
            laps: [],
            quickest: null,
            slowest: null,
        })
    }

    // This method will pause the stopwatch
    handleStop = () => {
        clearInterval(this.interval);
        this.setState(state => ({
            stopwatchOn: !state.stopwatchOn,
        }))
    }

    // This method will start the stopwatch
    handleStart = () => {
        let seconds = this.state.seconds;
        let minutes = this.state.minutes;

        this.interval = setInterval(() => {
            seconds++;
            if(seconds >= 60){
                seconds = 0
                minutes++;
            }
            this.setState({
                seconds: seconds,
                minutes: minutes,
            })
        }, 1000);

        this.setState(state => ({
            stopwatchOn: !state.stopwatchOn,
        }))
    }

    render(){
        return(
            <React.Fragment>
                <TabsComponent location={2} />
                <Fade timeout={500} in>
                    <div className={Style.Container}>
                        <div className={Style.Time}>
                            <Typography variant="h1" color="primary" align="center">
                                {this.state.minutes >= 0 && this.state.minutes < 10 ? String(`0${this.state.minutes}`) : this.state.minutes}
                            </Typography>
                            <Typography variant="h2" color="primary" align="center">:</Typography>
                            <Typography variant="h1" color="primary" align="center">
                                {this.state.seconds >= 0 && this.state.seconds < 10 ? String(`0${this.state.seconds}`) : this.state.seconds}
                            </Typography>
                        </div>
                        <Zoom timeout={500} in>
                            <div className={Style.ButtonGroup}>
                                {
                                    this.state.stopwatchOn === true 
                                        ? <Button onClick={this.handleLap} className={Style.GreyBtn} variant="outlined" size="large" >Lap</Button>
                                            : <Button onClick={this.handleReset} className={Style.GreyBtn} variant="outlined" size="large" >Reset</Button>
                                }
                                {
                                    this.state.stopwatchOn === true 
                                        ? <Button onClick={this.handleStop} className={Style.RedBtn} variant="outlined" size="large" >Stop</Button>
                                            : <Button onClick={this.handleStart} className={Style.GreenBtn} variant="outlined" size="large" >Start</Button>
                                }
                            </div>
                        </Zoom>
                        <List className={Style.List}>
                            {
                                this.state.laps.map(lap => {
                                    return(
                                        <ListItem key={lap.id} button divider onClick={() => this.handleUpdateStopwatch(lap.index)}>
                                            <ListItemText align="left" secondary={`Lap ${lap.index}`} />
                                            {/* <div style={{flex: "1"}} /> */}
                                            <ListItemText 
                                                align="right" 
                                                primary={`${lap.minutes >= 0 && lap.minutes < 10 ? String(`0${lap.minutes}`) : lap.minutes}:${lap.seconds >= 0 && lap.seconds < 10 ? String(`0${lap.seconds}`) : lap.seconds}`} 
                                                className={lap.id === this.state.quickest.id && lap.id === this.state.slowest.id ? Style.Normal : lap.id === this.state.quickest.id ? Style.Fastest 
                                                                : lap.id === this.state.slowest.id  ? Style.Slowest : Style.Normal}
                                            />
                                        </ListItem>
                                    );
                                })
                            }
                        </List>
                    </div>
                </Fade>
            </React.Fragment>
        );
    }
}