import React from 'react';

// Material UI Components
import { List, ListItem, ListItemText, Typography, ListItemSecondaryAction, IconButton, Switch, Tooltip } from '@material-ui/core';

// Style sheet
import Style from './List.module.scss';

// Icons
import EditIcon from '@material-ui/icons/Edit';
import DeleteForeverIcon from '@material-ui/icons/DeleteForever';

export function ListComponent({ alarms, edit, handleAlarmEdit, handleAlarmDelete, handleSwitchChange }){
    return(
        <List className={Style.List}>
            {
                alarms.length === 0 
                
                ? <ListItem className={Style.Center}>
                    <ListItemText
                        primary="No alarms found. Please add alarm."
                    />
                </ListItem>
                :
                alarms.map(alarm => {
                    return(
                        <ListItem key={alarm.id} divider>
                            <ListItemText
                                children={<Typography className={alarm.active === true ? Style.Active : Style.Inactive} variant="h4">{`${alarm.hours >= 0 && alarm.hours < 10 ? String(`0${alarm.hours}`) : alarm.hours}:${alarm.minutes >= 0 && alarm.minutes < 10 ? String(`0${alarm.minutes}`) : alarm.minutes}`}</Typography>}
                                secondary={alarm.label}
                            />
                            <ListItemSecondaryAction edge="end">
                                {
                                    edit === true 
                                        ? <React.Fragment>
                                            <Tooltip title={`Edit ${alarm.label} alarm`} aria-label={`Edit ${alarm.label} alarm`} placement="top">
                                                <span>
                                                    <IconButton onClick={() => handleAlarmEdit(alarm)} className={Style.GreyBtn} aria-label={`Edit ${alarm.label} alarm`}><EditIcon /></IconButton>
                                                </span>
                                            </Tooltip>
                                            <Tooltip title={`Delete ${alarm.label} alarm`} aria-label={`Delete ${alarm.label} alarm`} placement="top">
                                                <span>
                                                    <IconButton onClick={() => handleAlarmDelete(alarm)} className={Style.RedBtn} aria-label={`Delete ${alarm.label} alarm`}><DeleteForeverIcon /></IconButton>
                                                </span>
                                            </Tooltip>
                                        </React.Fragment> 
                                        : <Switch size="medium" color="primary" onChange={() => handleSwitchChange(alarm)} checked={alarm.active === true ? true : false} />
                                }
                            </ListItemSecondaryAction>
                        </ListItem>
                    );
                })
            }
        </List>
    );
}