import React from 'react';

// Material UI Components
import { Dialog, DialogTitle } from '@material-ui/core';

// Layouts
import {Add} from './layouts/Add/Add';
import {Delete} from './layouts/Delete/Delete';
import {Alarm} from './layouts/Alarm/Alarm';

export function DialogComponent({alarmPicked, dialog, dialogType, hours, minutes, handleTime, onChangeTextfield, handleSave, handleDialogClose, handleDelete, handleSaveEdit, handleStop}){
    return(
        <Dialog
            open={dialog}
            keepMounted
            onClose={handleDialogClose}
            aria-labelledby="alertDialogTitle"
            fullWidth={true}
            maxWidth="xs"
        >
            <DialogTitle id="alertDialogTitle">
                {
                    dialogType === "Add" ? "Add new Alarm" : dialogType === "Del" ? "Delete?" : dialogType === "Edit" ? "Edit Alarm" : `${alarmPicked === null ? "Alarm" : alarmPicked.label} Complete!` 
                } 
            </DialogTitle>
            {
                dialogType === "Add" 
                ? <Add 
                    alarmPicked={alarmPicked}
                    hours={hours} 
                    minutes={minutes} 
                    handleTime={handleTime}
                    onChangeTextfield={onChangeTextfield}
                    handleDialogClose={handleDialogClose}
                    handleSave={handleSave} />
                : dialogType === "Del" 
                ? <Delete 
                    alarmPicked={alarmPicked}
                    handleDialogClose={handleDialogClose}
                    handleDelete={handleDelete} />
                : dialogType === "Edit" 
                ? <Add 
                    alarmPicked={alarmPicked}
                    hours={hours} 
                    minutes={minutes} 
                    handleTime={handleTime}
                    onChangeTextfield={onChangeTextfield}
                    handleDialogClose={handleDialogClose}
                    handleSaveEdit={handleSaveEdit} />
                : <Alarm 
                    alarmPicked={alarmPicked}
                    handleStop={handleStop} />
            }
        </Dialog>
    );
}