import React from 'react';

// Material UI Components
import {DialogContent, DialogContentText, DialogActions, Button } from '@material-ui/core';

// Style sheet
import Style from './Delete.module.scss';

export function Delete({alarmPicked, handleDialogClose, handleDelete}){
    return(
        <React.Fragment>
            <DialogContent>
                <DialogContentText>Are you sure you want to delete {alarmPicked.label}</DialogContentText>
            </DialogContent>
            <DialogActions disableSpacing>
                <Button className={Style.GreyBtn} onClick={handleDialogClose}>No</Button>
                <Button className={Style.RedBtn} onClick={() => handleDelete(alarmPicked.id)}>Yes</Button>
            </DialogActions>
        </React.Fragment>
    );
}