import React from 'react';

// Material UI Components
import { Zoom, Paper, Tooltip, Button } from '@material-ui/core';

// Style sheet
import Style from './Paper.module.scss';

// Icons
import AddIcon from '@material-ui/icons/Add';

export function PaperComponent({ alarms, edit, handleDialogOpen, handleEdit, handleClearAlarms }){
    return(
        <Zoom timeout={500} in>
            <Paper className={Style.Paper} variant="outlined" >
                <div>
                    <Tooltip title={ alarms.length === 0 ? "" : edit === true ? "Edit Mode Off" : "Edit Mode On"} aria-label="Edit Mode" placement="top">
                        <span>
                            <Button aria-label="Edit Mode" onClick={handleEdit} className={alarms.length === 0 ? "" : edit === true ? Style.PrimaryBtn : Style.BlackBtn} size="large" disabled={alarms.length === 0 ? true : false}>Edit</Button>
                        </span>
                    </Tooltip> 
                    <Tooltip title={edit === true ? "Clear Alarms" : ""} aria-label="Clear Alarms" placement="top">
                        <span>
                            <Button aria-label="Clear Alarms" onClick={handleClearAlarms} className={edit === true ? `${Style.RedBtn} ${Style.Visible}` : Style.Hidden} size="large">Clear</Button>
                        </span>
                    </Tooltip> 
                </div>
                <Tooltip title="Add Alarm" aria-label="Add Alarm" placement="top">
                    <Button aria-label="Add Alarm" onClick={() => handleDialogOpen("Add")} className={Style.BlackBtn} size="large"><AddIcon /></Button>
                </Tooltip>
            </Paper>
        </Zoom>
    );
}