import React from 'react';
import PropTypes from 'prop-types';

// Material UI Components
import { Fade} from '@material-ui/core';

// Components
import {TabsComponent} from '../Tabs/Tabs';
import {ListComponent} from './components/List/List';
import {PaperComponent} from './components/Paper/Paper';
import {DialogComponent} from './components/Dialog/Dialog';

// Style sheet
import Style from './Alarm.module.scss';

// Sound
import Sound from '../../sound/beep.wav';

export class Alarm extends React.Component{
    constructor(props){
        super(props);
        this.interval = null;
        this.state = {
            alarms: [],
            alarmPicked: null,
            edit: false, 
            newAlarmHours: 0,
            newAlarmMinutes: 0,
            newAlarmLabel: "",
            dialog: false,
            dialogType: "",
        }
    }

    static propTypes = {
        alarms: PropTypes.array,
        alarmPicked: PropTypes.object,
        edit: PropTypes.bool,
        newAlarmHours: PropTypes.number,
        newAlarmMinutes: PropTypes.number,
        newAlarmLabel: PropTypes.string,
        dialog: PropTypes.bool,
        dialogType: PropTypes.string,
    }

    // This method will check every second if the current time is equal to the time of the alarm if so it will open the finish dialog
    checkAlarmComplete = (alarms) => {
        this.interval = setInterval(() => {
            const date = new Date();
            const hours = date.getHours();
            const minutes = date.getMinutes();
    
            const active = alarms.filter(alarm => alarm.active === true)

            console.log("Hello");

            active.forEach(item => {
                if(item.hours === hours && item.minutes === minutes){
                    this.playSound();
                    this.handleDialogOpen("Finish", item);
                }
            })
        }, 1000);
    }

    componentWillUnmount(){
        clearInterval(this.interval);
    }

    componentDidMount(){ 
        clearInterval(this.interval);

        this.audio = new Audio(Sound);

        // Will set a variables with the value of the local storage 
        const setAlarms = JSON.parse(localStorage.getItem("Alarms") || "[]");

        this.checkAlarmComplete(setAlarms);

        if(this.state.dialog === false){
            this.audio.pause();
        }

        this.setState({
            alarms: setAlarms,
        })
    }
    
    // This method will play the sound
    playSound = () => {
        clearInterval(this.interval);
        this.audio.play();
        this.audio.loop = true;
    }

    // This method will change the newAlarmLabel to whatever the user inputs
    handleChangeTextfield = (event) => {
        this.setState({
            newAlarmLabel: event.target.value,
        })
    }

    // This method will open the edit popup
    handleAlarmEdit = (alarm) => {
        this.setState({
            alarmPicked: alarm,
        })

        this.handleDialogOpen("Edit", alarm);
    }

    // This method will open the delete popup
    handleAlarmDelete = (alarm) => {
        this.setState({
            alarmPicked: alarm,
        })

        this.handleDialogOpen("Del");
    }

    // This method will update the switch to either be active true or false
    handleSwitchChange = (alarm) => {
        const newAlarms = [...this.state.alarms];

        // Create new array from newAlarms array with the element that has the same id and will update the active attribute
        const updateAlarm = newAlarms.filter(item => {
            if(item.id === alarm.id){
                item.active = !item.active
            }
            return item
        });

        // Will update the item with the updated data
        newAlarms.forEach(item => {
            if(item.id === updateAlarm.id){
                item = updateAlarm
            }
        });

        // Setting the Alarms array into our local storage
        localStorage.setItem("Alarms", JSON.stringify(newAlarms));

        this.setState({
            alarms: newAlarms
        })

        this.componentDidMount();
    }

    // This method will update the timer depending on what button the users clicks on it will either decrease or increase the timer
    handleTime = (type, clockType) => {
        let hours = this.state.newAlarmHours;
        let minutes = this.state.newAlarmMinutes;

        if(type === "Add"){
            switch(clockType){
                case "Hour":
                    hours++;
                    this.setState(state => ({
                        newAlarmHours: hours,
                    }))
                    break;
                case "Minute":
                    minutes++;
                    this.setState(state => ({
                        newAlarmMinutes: minutes,
                    }))
                    break;
                default:
                    break;
            }
        }else if(type === "Subtract"){
            switch(clockType){
                case "Hour":
                    hours--;
                    this.setState(state => ({
                        newAlarmHours: hours,
                    }))
                    break;
                case "Minute":
                    minutes--;
                    this.setState(state => ({
                        newAlarmMinutes: minutes,
                    }))
                    break;
                default:
                    break;
            }
        }
    }

    // This method will save a new alarm into the alarm array
    handleSave = () => {
        const hours = this.state.newAlarmHours;
        const minutes = this.state.newAlarmMinutes;
        const label = (this.state.newAlarmLabel === "") ? "Label" : this.state.newAlarmLabel;

        // Getting the current state of alarms array
        const newAlarms = [...this.state.alarms];
        
        // Creating new alarm
        const newAlarm = {
            id: Math.random(),
            label: label,
            hours: hours,
            minutes: minutes,
            active: true
        }

        // Putting the new alarm into the newAlarms array
        newAlarms.push(newAlarm);

        // Setting the Alarms array into our local storage
        localStorage.setItem("Alarms", JSON.stringify(newAlarms));

        this.setState({
            alarms: newAlarms,
            newAlarmHours: 0,
            newAlarmMinutes: 0,
            newAlarmLabel: "",
        })

        this.handleDialogClose();
    }

    // This method will update the state with new values that the user chooses
    handleSaveEdit = (alarm) => {
        const hours = this.state.newAlarmHours;
        const minutes = this.state.newAlarmMinutes;
        const label = this.state.newAlarmLabel;

        // Getting the current state of alarms array
        const newAlarms = [...this.state.alarms];

        // Create new array from newAlarms array with the element that has the same id and will update its data
        const updateAlarm = newAlarms.filter(item => {
            if(item.id === alarm.id){
                item.hours = hours;
                item.minutes = minutes;
                item.label = label;
            }
            return item
        });
        
        // Will update the item with the updated data
        newAlarms.forEach(item => {
            if(item.id === updateAlarm.id){
                item = updateAlarm
            }
        });

        // Setting the Alarms array into our local storage
        localStorage.setItem("Alarms", JSON.stringify(newAlarms));
        
        this.setState(state => ({
            alarms: newAlarms,
            newAlarmHours: 0,
            newAlarmMinutes: 0,
            newAlarmLabel: "",
            edit: !state.edit, 
        }))
        
        this.handleDialogClose();
    }

    // This method will change the edit state 
    handleEdit = () => {
        this.setState(state => ({
            edit: !state.edit,
        }))
    }

    // This method will delete the location from the state
    handleDelete = (id) => {
        const itemId = id;

        // Getting the current state of alarms array
        const currentAlarms = [...this.state.alarms];

        // Remove the item from the new alarms array
        const newAlarms = currentAlarms.filter(item => {
            return item.id !== itemId
        });

        // Setting the Alarms array into our local storage
        localStorage.setItem("Alarms", JSON.stringify(newAlarms));

        this.setState({
            alarms: newAlarms,
        })

        this.handleDialogClose();
    }

    // This method will clear the alarms and the local storage
    handleClearAlarms = () => {
        // Removing the Alarms arrays from the local storage
        localStorage.removeItem("Alarms");

        this.setState({
            alarms: [],
            alarmPicked: null,
            edit: false, 
            newAlarmHours: 0,
            newAlarmMinutes: 0,
            newAlarmLabel: "",
            dialog: false,
            dialogType: "",
        })
    }

    // This method will stop the alarm once it has been completed and the user clicks on the stop button
    handleStop = (alarm) => {
        this.audio.pause();

        // Get current state of alarms array
        const newAlarms = [...this.state.alarms];

        // Create new array from newAlarms array with the element that has the same id
        const updateAlarm = newAlarms.filter(item => {
            if(item.id === alarm.id){
                item.active = !item.active
            }
            return item
        });

        // Will update the item with the updated data
        newAlarms.forEach(item => {
            if(item.id === updateAlarm.id){
                item = updateAlarm
            }
        });

        // Setting the Alarms array into our local storage
        localStorage.setItem("Alarms", JSON.stringify(newAlarms));

        this.setState({
            alarms: newAlarms
        })

        this.handleDialogClose();
    }

    // This method will open the popup
    handleDialogOpen = (dialogType, alarmPicked) => {
        this.setState(state => ({
            dialog: !state.dialog,
            dialogType: dialogType,
        }))

        if(dialogType === "Finish"){
            this.setState({
                alarmPicked: alarmPicked
            })
        }

        if(dialogType === "Edit"){
            this.setState({
                newAlarmHours: alarmPicked.hours,
                newAlarmMinutes: alarmPicked.minutes,
                newAlarmLabel: alarmPicked.label,
            })
        }
    }

    // This method will close the popup
    handleDialogClose = () => {
        this.setState(state => ({
            alarmPicked: null,
            dialog: !state.dialog,
            dialogType: "",
        }))
    }

    render(){
        return(
            <React.Fragment>
                <TabsComponent location={1} />
                <Fade timeout={500} in>
                    <div className={Style.Container}>
                        <ListComponent 
                            alarms={this.state.alarms} 
                            edit={this.state.edit} 
                            handleAlarmEdit={this.handleAlarmEdit} 
                            handleAlarmDelete={this.handleAlarmDelete} 
                            handleSwitchChange={this.handleSwitchChange} />
                        <PaperComponent 
                            alarms={this.state.alarms} 
                            edit={this.state.edit} 
                            handleEdit={this.handleEdit} 
                            handleClearAlarms={this.handleClearAlarms} 
                            handleDialogOpen={this.handleDialogOpen} />
                    </div>
                </Fade>
                <DialogComponent 
                    alarmPicked={this.state.alarmPicked}
                    dialog={this.state.dialog} 
                    dialogType={this.state.dialogType} 
                    hours={this.state.newAlarmHours} 
                    minutes={this.state.newAlarmMinutes} 
                    onChangeTextfield={this.handleChangeTextfield} 
                    handleTime={this.handleTime} 
                    handleDialogClose={this.handleDialogClose} 
                    handleSave={this.handleSave} 
                    handleSaveEdit={this.handleSaveEdit} 
                    handleStop={this.handleStop}
                    handleDelete={this.handleDelete} />
            </React.Fragment>
        );
    }
}