import React from 'react';
import PropTypes from 'prop-types';

// Material UI Components
import { Fade } from '@material-ui/core';

// Components
import {TabsComponent} from '../Tabs/Tabs';
import {Content} from './components/Content/Content';
import {PaperComponent} from './components/Paper/Paper';
import {DialogComponent} from './components/Dialog/Dialog';

// Style sheet
import Style from './Clock.module.scss';

export class Clock extends React.Component{
    constructor(props){
        super(props);
        this.interval = null;
        this.state = {
            selectedLocation: "",
            selectedLocationError: false,
            newLocation: null,
            locations: [],
            cities: [],
            locationPicked: null,
            dialog: false,
            dialogType: "",
            currentMinute: null,
        }
    }

    static propTypes = {
        selectedLocation: PropTypes.string,
        selectedLocationError: PropTypes.bool,
        newLocation: PropTypes.object,
        locations: PropTypes.object,
        cities: PropTypes.object,
        locationPicked: PropTypes.object,
        dialog: PropTypes.bool,
        dialogType: PropTypes.string,
        currentMinute: PropTypes.number,
    }

    // This method will create the local time object
    createLocal = () => {
        const date = new Date();
        
        const dataString = date.toString();
        const dataArray = dataString.split("");

        const offset = dataArray.splice(29, 4); 
        // const operator = dataArray[28]
        const removeZero = offset.filter(num => num !== "0");
        const gmt = parseInt(removeZero.join(""));
        const converGmtToSeconds = gmt * 60 * 60;
        // const joinOffset = String(operator + converGmtToSeconds)

        const local = {
            name: "Local",
            timezone: converGmtToSeconds,
            sys: {
                country: "Country"
            }
        }

        return local
    }

    // This method will check if the minute changed, if so it will re render the locations state 
    checkTime = () => {
        const currentMinute = this.state.currentMinute;

        this.interval = setInterval(() => {
            let date = new Date();
            let minute = date.getMinutes();

            if(currentMinute !== minute){
                this.setState({
                    locations: this.state.locations,
                    currentMinute: minute
                })
            }
        }, 1000);
    }
    
    // This method will clear the interval once the user exits from the page
    componentWillUnmount(){
        clearInterval(this.interval);
    }

    componentDidMount(){ 
        clearInterval(this.interval);

        this.checkTime();

        // Will set a variables with the value of the local storage 
        const setLocations = JSON.parse(localStorage.getItem("LocalLocations") || "[]");
        const setCities = JSON.parse(localStorage.getItem("LocalCities") || "[]");

        // Will check whether setLocations, setCities is empty or setLocations has atleast 1 location 
        if(setLocations.length === 0 || setCities.length === 0 || setLocations.length === 1){
            const newLocations = [];
            const local = this.createLocal()

            newLocations.push(local);

            this.setState({
                locations: newLocations,
                locationPicked: local,
            })
            return
        // If not empty, will update the locations with the current data 
        } else{
            this.setState({
                locations: setLocations,
                cities: setCities,
            })
        }

    }

    // This method will change the selectedLocation to whatever the user inputs
    handleLocationChange = (event) => {
        this.setState({
            selectedLocation: event.target.value,
        })
    }

    // This method will handle the text field when the user clicks on it and will set the selectedLocation to empty
    handleFocus = () => {
        if(this.state.selectedLocationError === true || this.state.selectedLocation === "Correct Input"){
            this.setState(state => ({
                selectedLocation: "",
                selectedLocationError: !state.selectedLocationError,
            }))
        }
    }

    // This method will handle the key that the user presses
    handleKeyPress = (event) => {
        this.handleFocus(); 
        if(this.state.dialog === true){
            if(event.key === 'Enter'){
                this.handleSubmit(); 
            } else if(event.key === 'Escape'){
                this.handleDialogClose()
            }
        }
    }

    // This method will fetch the api with the location that the user chooses
    handleSubmit = () => {
        const location = this.state.selectedLocation;
        const key = "f3ef21c55d165f4433893081d602af6e";
    
        fetch(`https://api.openweathermap.org/data/2.5/weather?q=${location}&APPID=${key}`)
        .then(response => response.json())
        .then(data => {
          this.setState({
            newLocation: data,
          })
          this.handleUpdate();
        })
        .catch(Error);
    }

    // This method will validate the users input and will either add the location or displays and error
    handleUpdate = () => {
        const errors = ["400", "401", "403", "404", "405", "409", "500", "503"];
    
        // Getting current state of locations list
        const newLocations = [...this.state.locations];
    
        // Getting current state of cities list & current input by user
        const currentCities = [...this.state.cities];
        
        // Checking if an error has occurred to display the incorrect message
        if(this.state.newLocation.cod === errors.find(number => number === this.state.newLocation.cod)){
          this.setState(state => ({
            selectedLocation: "Incorrect Input",
            selectedLocationError: !state.selectedLocationError,
            newLocation: null,
          }))
          return
        }else if(this.state.newLocation.name.toLowerCase() === this.state.cities.find(name => name === this.state.newLocation.name.toLowerCase())){
          this.setState(state => ({
            selectedLocation: "City already inserted",
            selectedLocationError: !state.selectedLocationError,
            newLocation: null,
          }))
          return
        }else{
            // Adding the new location selected in to the locations Array
            newLocations.push(this.state.newLocation);
            
            // Adding the input from the user to the new cities list
            currentCities.push(this.state.selectedLocation.toLowerCase());

            // Setting the Locations array into our local storage
            localStorage.setItem("LocalLocations", JSON.stringify(newLocations));

            // Setting the Cities array into our local storage
            localStorage.setItem("LocalCities", JSON.stringify(currentCities));
            
            // Setting the locations array with the new locations array & the cities array with the new cities array
            this.setState({
                selectedLocation: "Correct Input",
                newLocation: null,
                locations: newLocations,
                cities: currentCities,
            })
        }
    }

    // This method will display the data from the location that the user chooses
    handleOpenLocationData = (location) => {
        this.setState({
            locationPicked: location
        })
        this.handleDialogClose();
    }

    // This method will clear the state and the local storage
    handleClearLocations = () => {
        // Removing the Locations & Cities arrays from the local storage
        localStorage.removeItem("LocalLocations");
        localStorage.removeItem("LocalCities");

        this.setState({
            selectedLocation: "",
            selectedLocationError: false,
            newLocation: null,
            locations: [],
            cities: [],
        })

        this.componentDidMount()
    }

    // This method will delete the location from the state
    handleDelete = (id, name) => {
        const itemId = id;
        const itemName = name.toLowerCase();

        // Getting the current state of locations array
        const currentlocations = [...this.state.locations];

        // Remove the item from the new locations array
        const newlocations = currentlocations.filter(item => {
            return item.id !== itemId
        });

        // Getting the current state of cities array
        const currentCities = [...this.state.cities];
        
        // Remove the item from the new cities array
        const newCities = currentCities.filter(item => {
        return item !== itemName
        });

        // Setting the Locations array into our local storage
        localStorage.setItem("LocalLocations", JSON.stringify(newlocations));

        // Setting the Cities array into our local storage
        localStorage.setItem("LocalCities", JSON.stringify(newCities));

        this.setState({
            locationPicked: this.state.locations[0],
            locations: newlocations,
            cities: newCities,
        })

        this.componentDidMount();
        this.handleDialogClose();
    }

    // This method will open the popup
    handleDialogOpen = (dialogType) => {
        this.setState(state => ({
            dialog: !state.dialog,
            dialogType: dialogType,
        }))
    }

    // This method will close the popup
    handleDialogClose = () => {
        this.setState(state => ({
            dialog: !state.dialog,
        }))
    }

    render(){
        return(
            <React.Fragment>
                <TabsComponent location={0} />
                <Fade timeout={500} in>
                    <div className={Style.Container}>
                        <Content local={this.createLocal()} locationPicked={this.state.locationPicked} />
                        <PaperComponent locationPicked={this.state.locationPicked} handleDialogOpen={this.handleDialogOpen} />
                    </div>
                </Fade>
                <DialogComponent 
                        dialog={this.state.dialog} 
                        dialogType={this.state.dialogType} 
                        selectedLocation={this.state.selectedLocation} 
                        selectedLocationError={this.state.selectedLocationError} 
                        locations={this.state.locations} 
                        locationPicked={this.state.locationPicked} 
                        handleOpenLocationData={this.handleOpenLocationData}
                        handleClearLocations={this.handleClearLocations}
                        handleDialogClose={this.handleDialogClose}
                        handleKeyPress={this.handleKeyPress}
                        onChange={this.handleLocationChange}
                        handleSubmit={this.handleSubmit} 
                        handleDelete={this.handleDelete} />
            </React.Fragment>
        );
    }
}