import React from 'react';

// Material UI Components
import { Zoom, Button } from '@material-ui/core';

// Style sheet
import Style from './ButtonGroup.module.scss';

export function ButtonGroup({timerOn, seconds, minutes, hours, handleCancel, handleStop, handleStart}){
    return(
        <Zoom timeout={500} in>
            <div className={Style.ButtonGroup}>
                <Button 
                    onClick={handleCancel} 
                    className={`${Style.GreyBtn} ${Style.Left}`} 
                    variant="outlined" 
                    size="large" >
                        Cancel
                </Button>
                {
                    timerOn === true 
                        ? <Button 
                            onClick={handleStop} 
                            className={`${Style.RedBtn} ${Style.Right}`} 
                            variant="outlined" 
                            size="large" >
                                Stop
                        </Button>
                        : <Button 
                            onClick={handleStart} 
                            className={seconds === 0 && minutes === 0 && hours === 0 ? `${Style.GreyBtn} ${Style.Right}` : `${Style.GreenBtn} ${Style.Right}`} 
                            variant="outlined" 
                            size="large" 
                            disabled={seconds === 0 && minutes === 0 && hours === 0 ? true : false}>
                                Start
                        </Button>
                }
            </div>
        </Zoom>
    );
}