import React from 'react';
import {Link} from "react-router-dom";

// Material UI Components
import { Paper, Tabs, Tab, } from '@material-ui/core';

// Components

// Style sheet

export function TabsComponent({location}){
    return(
      <Paper elevation={3} variant="outlined">
        <Tabs
          value={location}
          indicatorColor="primary"
          textColor="primary"
          centered
        >
          <Tab exact="true" to="/" label="Clock" component={Link} />
          <Tab to="/alarm" label="Alarm" component={Link} />
          <Tab to="/stopwatch" label="Stopwatch" component={Link} />
          <Tab to="/timer" label="Timer" component={Link} />
        </Tabs>
      </Paper>
    );
}