import React from 'react';

// Material UI Components
import {DialogContent, TextField, DialogActions, Button } from '@material-ui/core';

// Style sheet
import Style from './Add.module.scss';

export function Add({selectedLocation, selectedLocationError, handleDialogClose, handleKeyPress, onChange, handleSubmit}){
    return(
        <React.Fragment>
            <DialogContent>
                <TextField
                    error={selectedLocationError}
                    label="City"
                    placeholder='Try "London"'
                    type="string"
                    size="small"
                    variant="outlined"
                    color="primary"
                    fullWidth
                    value={selectedLocation}
                    onChange={onChange}
                    onKeyPress={handleKeyPress}
                />
            </DialogContent>
            <DialogActions disableSpacing>
                <Button className={Style.GreyBtn} onClick={handleDialogClose}>Cancel</Button>
                <Button onClick={handleSubmit} color="primary">Submit</Button>
            </DialogActions>
        </React.Fragment>
    );
}