import React from 'react';

// Material UI Components
import { Dialog, DialogTitle } from '@material-ui/core';

// Layouts
import {Add} from './layouts/Add/Add';
import {ListComponent} from './layouts/List/List';
import {Delete} from './layouts/Delete/Delete';

export function DialogComponent({dialog, dialogType, selectedLocation, selectedLocationError, locations, locationPicked, handleOpenLocationData, handleDialogClose, handleClearLocations, handleKeyPress, onChange, handleDelete, handleSubmit}){
    return(
        <Dialog
            open={dialog}
            keepMounted
            onClose={handleDialogClose}
            aria-labelledby="alertDialogTitle"
            fullWidth={true}
            maxWidth="xs"
        >
            <DialogTitle id="alertDialogTitle">
                {
                    dialogType === "Add" ? "Add new location" : dialogType === "Del" ? "Delete?" : "Locations"
                }
            </DialogTitle>

            {
                dialogType === "Add" 
                ? <Add 
                    selectedLocation={selectedLocation} 
                    selectedLocationError={selectedLocationError} 
                    handleDialogClose={handleDialogClose}
                    handleKeyPress={handleKeyPress}
                    onChange={onChange}
                    handleSubmit={handleSubmit} /> 
                : dialogType === "Del" 
                ? <Delete 
                    locationPicked={locationPicked} 
                    handleDialogClose={handleDialogClose}
                    handleDelete={handleDelete} /> 
                : <ListComponent 
                    locations={locations}
                    handleOpenLocationData={handleOpenLocationData}
                    handleDialogClose={handleDialogClose} 
                    handleClearLocations={handleClearLocations} />
            }
            
        </Dialog>
    );
}