import React from 'react';
import PropTypes from 'prop-types';

// Material UI Components
import { Fade } from '@material-ui/core';

// Components
import {TabsComponent} from '../Tabs/Tabs';
import {Time} from './components/Time/Time';
import {ButtonGroup} from './components/ButtonGroup/ButtonGroup';
import {DialogComponent} from './components/Dialog/Dialog';

// Style sheet
import Style from './Timer.module.scss';

// Sound
import Sound from '../../sound/beep.wav';

export class Timer extends React.Component{
    constructor(props){
        super(props);
        this.interval = null;
        this.state = {
            timerOn: false,
            seconds: 0,
            minutes: 0,
            hours: 0,
            dialog: false,
            timer: null,
        }
    }

    static propTypes = {
        timerOn: PropTypes.bool,
        seconds: PropTypes.number,
        minutes: PropTypes.number,
        hours: PropTypes.number,
        dialog: PropTypes.bool,
        timer: PropTypes.object,
    }

    // This method will reset the state and will clear the interval once the user exits from the page
    componentWillUnmount(){
        this.handleCancel();
    }

    // This method will run each time the state is changed and it will check whether the timer is 0
    componentDidUpdate(){
        if(this.state.seconds === 0 && this.state.minutes === 0 && this.state.hours === 0 && this.state.timerOn === true){
            clearInterval(this.interval);
            this.setState({
                timerOn: false,
            })
            this.handleFinished();
        } 
    }

    // This method will update the timer depending on what button the users clicks on it will either decrease or increase the timer
    handleTime = (type, clockType) => {
        let hours = this.state.hours;
        let minutes = this.state.minutes;
        let seconds = this.state.seconds;

        if(type === "Add"){
            switch(clockType){
                case "Hour":
                    hours++;
                    this.setState(state => ({
                        hours: hours,
                    }))
                    break;
                case "Minute":
                    minutes++;
                    this.setState(state => ({
                        minutes: minutes,
                    }))
                    break;
                case "Second":
                    seconds++;
                    this.setState(state => ({
                        seconds: seconds,
                    }))
                    break;
                default:
                    break;
            }
        }else if(type === "Subtract"){
            switch(clockType){
                case "Hour":
                    hours--;
                    this.setState(state => ({
                        hours: hours,
                    }))
                    break;
                case "Minute":
                    minutes--;
                    this.setState(state => ({
                        minutes: minutes,
                    }))
                    break;
                case "Second":
                    seconds--;
                    this.setState(state => ({
                        seconds: seconds,
                    }))
                    break;
                default:
                    break;
            }
        }
    }

    // This method will reset the timer
    handleCancel = () => {
        clearInterval(this.interval);
        this.setState({
            timerOn: false,
            seconds: 0,
            minutes: 0,
            hours: 0,
            dialog: false,
            timer: null,
        })
    }

    // This method will pause the timer
    handleStop = () => {
        clearInterval(this.interval);
        this.setState(state => ({
            timerOn: !state.timerOn,
        }))
    }

    // This method will start the timer
    handleStart = () => {
        let seconds = this.state.seconds;
        let minutes = this.state.minutes;
        let hours= this.state.hours;

        const newTimer = {
            seconds: seconds,
            minutes: minutes,
            hours: hours,
        }

        this.setState({
            timer: newTimer,
        })

        this.interval = setInterval(() => {
            if(hours === 0 && minutes === 0 && seconds === 0){
                seconds = 0;
                minutes = 0;
                hours = 0;
            } else if(minutes !== 0 && seconds === 0){
                seconds = 60;
                minutes--;
            } else if(hours !== 0 && minutes === 0 && seconds === 0){
                seconds = 60;
                minutes = 59;
                hours--;
            }
            seconds--;

            this.setState({
                seconds: seconds,
                minutes: minutes,
                hours: hours,
            })
        }, 1000);

        this.setState(state => ({
            timerOn: !state.timerOn,
        }))
    }

    // This method will open a popup to let the user either restart the timer or stop
    handleFinished = () => {
        this.audio = new Audio(Sound);
        this.audio.play();
        this.audio.loop = true;
        this.setState(state => ({
            dialog: !state.dialog 
        }))
    }

    // This method will close the popup
    handleCloseDialog = () => {
        this.audio.pause();
        this.handleCancel();
    }

    // This method will restart the timer
    handleRepeat = () => {
        this.audio.pause();
        const currentTimer = this.state.timer;

        let seconds = currentTimer.seconds;
        let minutes = currentTimer.minutes;
        let hours= currentTimer.hours;

        this.setState(state => ({
            dialog: !state.dialog,
            seconds: seconds,
            minutes: minutes,
            hours: hours,
        }))
    }

    render(){
        return(
            <React.Fragment>
                <TabsComponent location={3} />
                    <Fade timeout={500} in>
                        <div className={Style.Container}>
                            <Time 
                                timerOn={this.state.timerOn} 
                                seconds={this.state.seconds} 
                                minutes={this.state.minutes} 
                                hours={this.state.hours} 
                                handleTime={this.handleTime} />
                            <ButtonGroup 
                                timerOn={this.state.timerOn} 
                                seconds={this.state.seconds} 
                                minutes={this.state.minutes} 
                                hours={this.state.hours} 
                                handleCancel={this.handleCancel} 
                                handleStop={this.handleStop} 
                                handleStart={this.handleStart} />
                            <DialogComponent 
                                dialog={this.state.dialog} 
                                handleCloseDialog={this.handleCloseDialog} 
                                handleRepeat={this.handleRepeat} />
                        </div>
                    </Fade>
            </React.Fragment>
        );
    }
}