import React from 'react';

// Material UI Components
import { Typography, Tooltip,  DialogContent, TextField, Box, DialogActions, Button } from '@material-ui/core';

// Style sheet
import Style from './Add.module.scss';

// Icons
import KeyboardArrowDownIcon from '@material-ui/icons/KeyboardArrowDown';
import KeyboardArrowUpIcon from '@material-ui/icons/KeyboardArrowUp';

export function Add({alarmPicked, hours, minutes, handleTime, onChangeTextfield, handleDialogClose, handleSave, handleSaveEdit}){
    return(
        <React.Fragment>
            <DialogContent>
                <div className={Style.Time}>
                    <div className={Style.TimeGroup}>
                        <Typography variant="overline" align="center">Hours</Typography>
                        <Tooltip 
                            className={Style.Tooltip} 
                            title={hours === 23 ? "" : "Add 1 hour"} 
                            aria-label="Add 1 hour" 
                            placement="top">
                            <span>
                                <Button 
                                    onClick={hours >= 0 && hours < 23 ? () => handleTime("Add", "Hour") : handleTime()} 
                                    className={hours >= 0 && hours < 23 ? Style.GreyBtn : ""} 
                                    variant="outlined" 
                                    size="medium" 
                                    fullWidth 
                                    disabled={hours >= 0 && hours < 23 ? false : true} >
                                        <KeyboardArrowUpIcon/>
                                </Button>
                            </span>
                        </Tooltip>
                        <Typography variant="h3" color="primary" align="center">
                            {hours >= 0 && hours < 10 ? String(`0${hours}`) : hours}
                        </Typography>
                        <Tooltip className={Style.Tooltip} title={hours === 0 ? "" : "Subtract 1 hour"} aria-label="Subtract 1 hour" placement="bottom">
                            <span>
                                <Button 
                                    onClick={hours === 0 ? handleTime() : () => handleTime("Subtract", "Hour")} 
                                    className={hours === 0 ? "" : Style.GreyBtn} 
                                    variant="outlined" 
                                    size="medium" 
                                    fullWidth 
                                    disabled={hours === 0 ? true : false} >
                                        <KeyboardArrowDownIcon />
                                </Button>
                            </span>
                        </Tooltip>
                    </div>
                    <Box mt={3}>
                        <Typography variant="h3" color="primary" align="center">:</Typography>
                    </Box>
                    <div className={Style.TimeGroup}>
                        <Typography variant="overline" align="center">Minutes</Typography>
                        <Tooltip className={Style.Tooltip} title={minutes === 59 ? "" : "Add 1 minute"} aria-label="Add 1 minute" placement="top">
                            <span>
                                <Button 
                                    onClick={minutes >= 0 && minutes < 59 ? () => handleTime("Add", "Minute") : handleTime()} 
                                    className={minutes >= 0 && minutes < 59 ? Style.GreyBtn : ""} 
                                    variant="outlined" 
                                    size="medium" 
                                    fullWidth 
                                    disabled={minutes >= 0 && minutes < 59 ? false : true} >
                                        <KeyboardArrowUpIcon />
                                </Button>
                            </span>
                        </Tooltip>
                        <Typography variant="h3" color="primary" align="center">
                            {minutes >= 0 && minutes < 10 ? String(`0${minutes}`) : minutes}
                        </Typography>
                        <Tooltip className={Style.Tooltip} title={minutes === 0 ? "" : "Subtract 1 minute"} aria-label="Subtract 1 minute" placement="bottom">
                            <span>
                                <Button 
                                    onClick={minutes === 0 ? handleTime() : () => handleTime("Subtract", "Minute")} 
                                    className={minutes === 0 ? "" : Style.GreyBtn} 
                                    variant="outlined" 
                                    size="medium" 
                                    fullWidth 
                                    disabled={minutes === 0 ? true : false} >
                                        <KeyboardArrowDownIcon />
                                </Button>
                            </span>
                        </Tooltip>
                    </div>
                </div>
                <Box m={3} />
                <TextField
                    label="Label"
                    placeholder={alarmPicked === null ? 'Try "Work"' : alarmPicked.label}
                    type="string"
                    size="small"
                    variant="outlined"
                    color="primary"
                    fullWidth
                    onChange={onChangeTextfield}
                />

            </DialogContent>
            <DialogActions disableSpacing>
                <Button className={Style.GreyBtn} onClick={handleDialogClose}>Cancel</Button>
                <Button onClick={alarmPicked === null ? handleSave : () => handleSaveEdit(alarmPicked)} color="primary">Save</Button>
            </DialogActions>
        </React.Fragment>
    );
}