import React from 'react';

// Material UI Components
import {List, ListItem, ListItemText, DialogActions, Button} from '@material-ui/core';

// Style sheet
import Style from './List.module.scss';

export function ListComponent({locations, handleOpenLocationData, handleDialogClose, handleClearLocations }){

    // Function to calculate the local time of the location
    function calcTime(timezone){
        const date = new Date();
        const time = date.setSeconds(timezone)
        const locationDate = new Date(time);

        const dateObject = {
            hour: locationDate.getUTCHours(),
            minute: locationDate.getUTCMinutes(),
        } 

        if(dateObject.minute >= 0 && dateObject.minute < 10){
            dateObject.minute = `0${dateObject.minute}`
        }

        if(dateObject.hour >= 0 && dateObject.hour < 10){
            dateObject.hour = `0${dateObject.hour}`
        }
        
        return `${dateObject.hour}:${dateObject.minute}`
    }

    return(
        <React.Fragment>
            <List>
                {
                    locations.map(location => {
                        return(
                            <ListItem key={Math.random()} button onClick={() => handleOpenLocationData(location)}>
                                <ListItemText align="left" primary={location.name === "Local" ? location.name : `${location.name} (${location.sys.country})`} />
                                <ListItemText align="right" secondary={calcTime(location.timezone)} />
                            </ListItem>
                        );
                    })        
                }
            </List>
            <DialogActions disableSpacing>
                <Button className={Style.GreyBtn} onClick={handleDialogClose}>Cancel</Button>
                <Button className={locations.length === 1 ? "" : Style.RedBtn} onClick={handleClearLocations} color="primary" disabled={locations.length === 1 ? true : false}>Clear</Button>
            </DialogActions>
        </React.Fragment>
    );
}