import React from 'react';

// Material UI Components
import { Dialog, DialogTitle, List, ListItem, ListItemText } from '@material-ui/core';

// Style sheet
import Style from './Dialog.module.scss';

export function DialogComponent({dialog, handleCloseDialog, handleRepeat}){
    return(
        <Dialog
            open={dialog}
            keepMounted
            onClose={handleCloseDialog}
            aria-labelledby="alertDialogTitle"
        >
            <DialogTitle id="alertDialogTitle">Timer Complete!</DialogTitle>
            <List className={Style.List}>
                <ListItem button onClick={handleCloseDialog}>
                    <ListItemText className={`${Style.ListBtn} ${Style.RedBtn}`} align="center" primary={"Stop"} />
                </ListItem>
                <ListItem button onClick={handleRepeat}>
                    <ListItemText className={`${Style.ListBtn} ${Style.GreyBtn}`} align="center" primary={"Repeat"} />
                </ListItem>
            </List>
        </Dialog>
    );
}